<template>
  <view class="container">
    <!-- 状态栏+导航栏合并（减少绿色区域核心） -->
    <view class="custom-nav" :style="{ height: statusBarHeight + navBarHeight + 'px' }">
      <!-- 状态栏占位（透明，不占用绿色区域） -->
      <view class="status-bar" :style="{ height: statusBarHeight + 'px' }"></view>
      
      <!-- 导航栏内容（高度压缩至32px） -->
      <view class="nav-content" :style="{ height: navBarHeight + 'px' }">
        <view class="nav-left" @click="goBack">
          <image src="/static/back.png" class="nav-icon" />
        </view>
        <view class="nav-title">医案库</view>
        <view class="nav-right">
          <image src="/static/more.png" class="nav-icon" @click="showMore" />
        </view>
      </view>
    </view>

    <!-- 搜索栏（紧贴导航栏，减少间距） -->
    <view class="search-bar">
      <image src="/static/search.png" class="search-icon" />
      <input 
        type="text" 
        class="search-input" 
        placeholder="搜索病症名称..." 
        placeholder-style="color: #999;"
      />
    </view>

    <!-- 主体内容（占比最大化） -->
    <view class="main-content">
      <view class="category-list">
        <view 
          class="category-item" 
          :class="{ active: item.active }" 
          v-for="(item, idx) in categories" 
          :key="idx" 
          @click="selectCategory(idx)"
        >
          {{ item.name }}
        </view>
      </view>

      <scroll-view class="tag-container" scroll-y>
        <view class="tag-grid">
          <view class="tag" v-for="(tag, idx) in currentTags" :key="idx">{{ tag }}</view>
        </view>
      </scroll-view>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      statusBarHeight: 0,
      navBarHeight: 32, // 导航栏主体高度压缩至32px（原38px，减少16%）
      categories: [
        { name: '内科', active: true },
        { name: '骨伤科', active: false },
        { name: '五官科', active: false },
        { name: '皮外伤科', active: false },
        { name: '急症', active: false },
        { name: '其他病症', active: false },
        { name: '儿科', active: false },
        { name: '妇科', active: false }
      ],
      categoryMap: {
        内科: [
          '细菌性痢疾', '阿米巴痢疾', '水肿', '原发性肾小球肾炎',
          '糖尿病', '尿崩症', '淋证', '肾盂肾炎',
          '尿道炎', '膀胱炎', '乳糜尿', '前列腺肥大',
          '男性不育症', '早泄', '阳强', '漏肩风',
          '贫血', '白细胞减少症', '低血压', '痹病',
          '萎证', '癫痫病', '狂病', '嗜睡'
        ],
        骨伤科: ['骨折', '脱位', '颈椎病', '腰椎间盘突出'],
        五官科: ['中耳炎', '鼻炎', '咽炎', '结膜炎'],
      },
      currentTags: []
    };
  },
  onLoad() {
    // 获取状态栏高度
    const systemInfo = uni.getSystemInfoSync();
    this.statusBarHeight = systemInfo.statusBarHeight;
    // 设置CSS变量（用于导航栏高度计算）
    document.documentElement.style.setProperty('--status-bar-height', this.statusBarHeight + 'px');
  },
  methods: {
    goBack() { uni.navigateBack({ delta: 1 }); },
    showMore() {
      uni.showActionSheet({ itemList: ['筛选', '排序', '反馈'] });
    },
    selectCategory(idx) {
      this.categories.forEach(item => item.active = false);
      this.categories[idx].active = true;
      this.currentTags = this.categoryMap[this.categories[idx].name] || [];
    }
  }
};
</script>

